import React from 'react';
import './feedbackForm.scss';

const FeedbackForm = ({titleText}) => {

  const send = (e) => {
    e.preventDefault()
  }

  return (
    <section className="feedback">
      <h2 className="feedback__title">{titleText}</h2>
      <form className="feedback__form">
        <div className="feedback__form-item">
          <input className="feedback__input" type="text" placeholder="–í–∞—à–µ –∏–º—è"/>
          <input className="feedback__input" type="text" placeholder="E-mail"/>
        </div>
        <textarea className="feedback__textarea" name="" id="" cols="10" rows="3" placeholder="–°–æ–æ–±—â–µ–Ω–∏–µ" />
        <button className="feedback__send" onClick={e => send(e)} type="submit">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</button>
      </form>
    </section>

  )
}

export default FeedbackForm;